<template>
    <yd-layout class="xunjian" style="position: relative; height: 100%;">
        <navBar title="测试灯箱" slot="navbar"></navBar>
        <yd-button @click.native="onPressLightbox($event)">显示灯箱</yd-button>
        <!--<div v-for="img,index in images" :data-index="index">-->
            <!--<img :src="img.url" @click="onPressLightbox($event)">-->
        <!--</div>-->
    </yd-layout>
</template>

<script>
    import {api_xj_submit,api_savejw} from '../../components/APIKeys';
    import {TMapGeometry} from "../../components/TMap";
    // import fancyBox from 'vue-fancybox';

    import 'swiper/dist/css/swiper.css'
    import { swiper, swiperSlide } from 'vue-awesome-swiper'

    export default {
        name: 'test_lightbox',
        components: {
            swiper, swiperSlide
        },
        props: [],
        data: function () {
            return {
                images : [
                    {url: 'http://img.pconline.com.cn/images/upload/upc/tx/photoblog/1309/15/c7/25716216_1379237027049.jpg', width: '100%'},
                    {url: 'http://static.ydcss.com/uploads/lightbox/meizu_s2.jpg', width: '100%'},
                    {url: 'http://static.ydcss.com/uploads/lightbox/meizu_s3.jpg', width: '100%'},
                    {url: 'http://static.ydcss.com/uploads/lightbox/meizu_s4.jpg', width: '100%'},
                    {url: 'http://static.ydcss.com/uploads/lightbox/meizu_s5.jpg', width: '100%'},
                    {url: 'http://static.ydcss.com/uploads/lightbox/meizu_s6.jpg', width: '100%'}
                ],
                swiperOption: {
                    slidesPerView: 1,
                    spaceBetween: 30,
                    mousewheel: true,
                    // pagination: {
                    //     el: '.swiper-pagination',
                    //     clickable: false
                    // }
                }
            };
        },
        computed: {

        },
        created: function () {
        },
        mounted: function () {

        },

        activated: function () {
            if (!!this.scrollOffset && this.scrollOffset > 0) {
                this.scrollView.scrollTop = this.scrollOffset;
            }
        },
        methods: {
            onPressLightbox(e) {
                let images = [
                    {url: 'http://img.pconline.com.cn/images/upload/upc/tx/photoblog/1309/15/c7/25716216_1379237027049.jpg'},
                    {url: 'http://static.ydcss.com/uploads/lightbox/meizu_s2.jpg'},
                    {url: 'http://static.ydcss.com/uploads/lightbox/meizu_s3.jpg'},
                    {url: 'http://static.ydcss.com/uploads/lightbox/meizu_s4.jpg'},
                    {url: 'http://static.ydcss.com/uploads/lightbox/meizu_s5.jpg'},
                    {url: 'http://static.ydcss.com/uploads/lightbox/meizu_s6.jpg'}
                ];

                this.$showLightbox(images);
                // fancyBox(e.target, images);
            },

            callback() {

            }
        },
    }
</script>

<style scoped>
    .swiper-box {
        width: 100%;
        height: 100%;
        margin: 0 auto;
    }
    .swiper-item {
        height: 100%;
        text-align: center;
        font-size: 18px ;
        background: #fff;
        /* Center slide text vertically */
        display: -webkit-box;
        display: -ms-flexbox;
        display: -webkit-flex;
        display: flex;
        -webkit-box-pack: center;
        -ms-flex-pack: center;
        -webkit-justify-content: center;
        justify-content: center;
        -webkit-box-align: center;
        -ms-flex-align: center;
        -webkit-align-items: center;
        align-items: center;
    }
</style>



